//     return timeStr.length > 10
// }

/**
 * @param {Number} timeStamp 
 * @param {Number} currentTime 
 * @returns {Boolean} 
 */
const isEarly = (timeStamp, currentTime) => {
  return timeStamp <= currentTime
}

/**
 * @param {Number} num 
 * @returns {String} 
 * @description 1010
 */
const getHandledValue = num => {
  return num < 10 ? '0' + num : num
}

/**
 * @param {Number} timeStamp 
 * @param {Number} startType 'year'
 */
const getDate = (timeStamp, startType) => {
  const d = new Date(timeStamp)
  const year = d.getFullYear()
  const month = getHandledValue(d.getMonth() + 1)
  const date = getHandledValue(d.getDate())
  const hours = getHandledValue(d.getHours())
  const minutes = getHandledValue(d.getMinutes())
  const second = getHandledValue(d.getSeconds())
  let resStr = ''
  if (startType === 'year') resStr = year + '-' + month + '-' + date + ' ' + hours + ':' + minutes + ':' + second
  else resStr = month + '-' + date + ' ' + hours + ':' + minutes
  return resStr
}

/**
 * @param {String|Number} timeStamp 
 * @returns {String} 
 */
export const getRelativeTime = (timeStamp, locale) => {
  // 
  // const IS_MILLISECOND = true;
  // 
  // if (IS_MILLISECOND) Math.floor(timeStamp /= 1000);
  // 
  // timeStamp = Number(timeStamp);
  // 
  // const currentTime = Math.floor(Date.parse(new Date()) / 1000);
  const currentTime = (new Date()).getTime()

  // 
  const IS_EARLY = isEarly(timeStamp, currentTime)
  // 
  let diff = currentTime - timeStamp
  // IS_EARLYfalse
  if (!IS_EARLY) diff = -diff
  let resStr = ''
  let dirStr = IS_EARLY ? (locale('i.time.before') || '') : (locale('i.time.after') || '')

  if (diff < 1000) resStr = locale('i.time.just') || ''
  // 59
  else if (diff < 60000) resStr = parseInt(diff / 1000) + (locale('i.time.seconds') || '') + dirStr
  // 595959
  else if (diff >= 60000 && diff < 3600000) resStr = Math.floor(diff / 60000) + (locale('i.time.minutes') || '') + dirStr
  // 5959235959
  else if (diff >= 3600000 && diff < 86400000) resStr = Math.floor(diff / 3600000) + (locale('i.time.hours') || '') + dirStr
  // 235959295959
  else if (diff >= 86400000 && diff < 2623860000) resStr = Math.floor(diff / 86400000) + (locale('i.time.days') || '') + dirStr
  // 295959364235959
  else if (diff >= 2623860000 && diff <= 31567860000 && IS_EARLY) resStr = getDate(timeStamp)
  else resStr = getDate(timeStamp, 'year')
  return resStr
}

export default function (timestamp, locale) {
  return getRelativeTime(timestamp, locale)
}
